.library{
    position: fixed;
    top: 0;
    left: 0;
    width: 28rem;
    height: 100%;
    background-color: white;
    box-shadow: 2px 2px 50px rgb(204, 204, 204);
    overflow: scroll;
    transform: translate(-100%);
    transition: all 0.5s ease;
    opacity: 0;
    h2{
        padding: 2rem;
      }
}

.library-song{
    display: flex ;
    align-items: center ;
    flex-direction: row;
    padding: 1rem 2rem 1rem 2rem;
    cursor: pointer;
    img{
        width: 30%;
    }
    &:hover{
        background-color: rgb(210, 221, 237);
    }
}

.song-description{
    padding-left: 1.3rem;
    h3{
        font-size: 1.2rem;
    }
    h4{
        font-size: 0.8rem;
    }
}

*{
    scrollbar-width: thin;
    scrollbar-color: rgba(155 , 155 , 155 , 0.2)transparent;
}

*::-webkit-scrollbar{
    width: 5px;
}

*::-webkit-scrollbar-track {
    background-color: transparent;
}

*::-webkit-scrollbar-thumb {
    background-color:rgba(88, 86, 86, 0.5) ;
    border-radius: 20px;
    border: transparent;
}

.selected{
    background-color: rgb(153, 208, 219);
}

.active-library{
    transform: translate(0%);
    opacity: 1;
}

@media screen and (max-width:768px) {
    .library{
        width: 100%;
    }
}